*{
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    --twitter-color:#50b7f5;
    --twitter-background: #e6ecf0;
}
.app{
    display: flex;
    height: 100vh;
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 10px;
}
.sidebar{
    border-right: 1px solid var(--twitter-background);
    flex: 0.3 1;
    margin-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    
    
}
.sidebar__tweet{
    background-color: var(--twitter-color) !important;
    color: white !important;
    border: none !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    border-radius: 30px !important;
    height:50px !important ;
    margin-top:20px !important;
}
.sidebar_icon{
    color:var(--twitter-color);
    font-size: 30px !important;
    margin-left: 20px;
    margin-bottom: 20px;
}

.sidebarOption{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.sidebarOption:hover{
    background-color:var(--twitter-background);
    border-radius: 30px;
    color: var(--twitter-color);
    transition: color 100ms ease-out;
}
.sidebarOption > .MuiSvgIcon-root{
    padding: 20px;
}
.sidebarOption >h2{
    font-weight: 800;
    font-size: 20px;
    margin-right: 20px;
}
.sidebarOption--active {
    color: var(--twitter-color);
  }
.feed{
    flex: 0.4 1;
    border-right: 1px solid var(--twitter-background);
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    overflow-y: scroll;
}
.feed::-webkit-scrollbar{
    display: none;
}
.feed{
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.feed__header{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    border: 1px solid var(--twitter-background);
    padding: 15px 20px;
}
.feed__header >h2{
    font-size: 20px;
    font-weight: 800;
}
.tweetBox{
    padding-bottom: 10px;
    padding-right: 10px;
    border-bottom: 8px solid var(--twitter-background);
}
.tweetBox > form{
    display: flex;
    flex-direction: column;
}
.tweetBox_input{
    padding: 20px;
    display:flex;
}
.tweetBox_input > input{
    flex:1px 1;
    margin-left: 20px;
    font-size: 20px;
    border: none;
}
.tweetBox_image{
    border: none;
    padding : 10px;
    margin-left: 10px;
}
.tweetBox_button{
    background-color: var(--twitter-color) !important;
    border: none !important;
    color: white !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    border-radius: 30px !important;
    width: 80px;
    height: 40px !important;
    margin-top: 20px !important;
    margin-left: auto !important;
}
.post{
    display: flex;
    align-items:flex-start ;
    border-bottom:1px solid var(--twitter-background) ;
    padding-bottom: 10px;
}
.post_body > img{
    border-radius: 20px;
    

}
.post_avatar{
    padding: 20px;
}
.post_body{
    flex: 1 1;
    padding: 10px;
}
.footer{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}
.header_verified{
    font-size: 14px !important;
    color: var(--twitter-color) !important;
}
.post__headerspecial{
    color: grey;
    font-size: 12px;
    font-weight: 600;
}
.header_text >h3{
   font-size: 15px;
   margin-bottom: 5px;
}
.body_text{
    margin-bottom: 10px;
    font-size: 15px;
}
.widgets{
    flex: 0.3 1;
}
.widgets_input{
    display: flex;
    align-items: center;
    background-color: var(--twitter-background);
    border-radius: 30px;
    padding:10px;
    margin-top: 10px;
    margin-left: 20px;

}
.search_input{
    border: none;
    background-color: var(--twitter-background);
}
.search_icon{
    color: gray !important;
}
.widget_container{
    margin-left: 20px;
    margin-top: 15px;
    border-radius: 20px;
    padding:20px;
    background-color: #f5f8fa;
}
.widget_container >h2{
    font-size: 18px;
    font-weight: 800;
}
.shareButton{
    margin-top: 20px !important;
}
